import React, { useState, useEffect } from "react";
import "../AddUser/AddUser.css";
import Axios from "axios";
import Input from "../../../components/Input/Input";
import Button from "../../../components/Button/Button";
import { useHistory } from "react-router-dom";
import Dropdown from "../../../components/Dropdown/Dropdown";
import { Link } from "react-router-dom";
import { ArrowLeftOutlined } from "@ant-design/icons";
import { Popconfirm, message } from "antd";



const AddUser = () => {
  useEffect(() => {
    fetchStateData();
    fetchZipData();
  }, []);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [address1, setAddress1] = useState("");
  const [address2, setAddress2] = useState("");
  const [city, setCity] = useState("");
  const [stateData, setStateData] = useState([]);
  const [state, setState] = useState("");
  const [zipData, setZipData] = useState([]);
  const [zip, setZip] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  
  const history = useHistory("");

  const save = (e) => {
    e.preventDefault();
    Axios.post("/user/admin/insert", {
      first_name: firstName,
      last_name: lastName,
      address1: address1,
      address2: address2,
      city: city,
      state: state,
      zip: zip,
      email: email,
      phone: phone,

    })
      .then((data) => {
        console.log(data);
        if(data.data.message =="User Already Exist!"){
        message.warn(data.data.message);
        }
        else
        {
          insertAuth(data.data.id);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const insertAuth = (id) => {
    Axios
    .post('/user/auth', {
      customer_id: id,
        username: email,
        password: password,
        crte_by: id,

    })
      .then((data) => {
        console.log(data);
        message.success(data.data.message);
 
        history.push("/manageuser");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const fetchStateData = () => {
    Axios.get("/user/admin/state/list")
      .then((data) => {
        setStateData(data.data);
        // console.log(data.data);
      })
      .catch((err) => console.log(err));
  };


  const fetchZipData = () => {
    Axios.get("/user/admin/zip/list")
      .then((data) => {
        setZipData(data.data);
        // console.log(data.data);
      })
      .catch((err) => console.log(err));
  };

 

  return (
    <div className="module-main-div">
      <div id="module-title-div">
        <Link to={"/manageuser/"}>
          <ArrowLeftOutlined style={{ fontSize: "22px", color: "#000" }} />
        </Link>
        <h1 className="module-title">Add User</h1>
      </div>

      <div className="module-inner-div">
        <form id="addProduct-form" onSubmit={(e) => save(e)}>
          <div
            style={{
              width: "100%",
              alignContent: "flex-start",
              marginLeft: -350,
            }}
          >
       
            <div>
              <Input
                autoFocus={true}
                label="First Name"
                required={true}
                type="none"
                onChange={(e) => setFirstName(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Last Name"
                  type="none"
                  onChange={(e) => setLastName(e.target.value)}
                />
              </div>
            </div>

            <div>
              <Input
                label="Address 1"
                required={true}
                type="none"
                onChange={(e) => setAddress1(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Address 2"
                  type="none"
                  onChange={(e) => setAddress2(e.target.value)}
                />

              </div>
            </div>

            <div>
            <Input
                  label="City"
                  type="none"
                  onChange={(e) => setCity(e.target.value)}
                />
              <div style={{ marginTop: -87, marginLeft: 450, width: "100%" }}>

            <Dropdown
                data={stateData}
                label="State"
                onChange={(e) => {
                  setState(e);
                }}
              />
                 
              </div>
            </div>

            <div>
              <Input
                label="E-mail"
                required={true}
                type="email"
                onChange={(e) => setEmail(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
              <Dropdown
                data={zipData}
                label="Zip"
                onChange={(e) => {
                  setZip(e);
                }}
              />
              </div>
            </div>

            <div>
              <Input
                label="Mobile"
                type="none"
                onChange={(e) => setPhone(e.target.value)}
              />
              <div style={{ marginTop: -101, marginLeft: 450, width: "100%" }}>
                <Input
                  label="Password"
                  type="password"
                  onChange={(e) => setPassword(e.target.value)}
                />

              </div>
            </div>

          </div>

         

          <Button label="Save" />
        </form>
      </div>
    </div>
  );
};

export default AddUser;
